.header--wrap{
    background-color: #f0967f;
}

.navbar{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
}
    .navbar .navbar--list{
        float: left;
        display: block;
        color: white;
        text-align: center;
        padding: 24px 36px;
        text-decoration: none;
    }
    .navbar .navbar--list:hover{
        background-color: darkturquoise;
    }
