/*==============Home  Page CSS ends here ======================*/
.container{
    display: grid;
    grid-template-columns: 20% auto;
}
.side--bar{
    padding:20px;
    background-color: #f3f3f3;
}
.side--bar input, select{
    border: 1px solid #cdcdcd;
    padding: 15px;
    width: 100%;
    box-sizing: border-box;
    margin: 16px 0px;
    border-radius: 10px;
}
.side--bar input:focus{
    border: 1px solid #16ccf5;
    outline: none;
}
.side--bar input:hover{
    border: 1px solid #16ccf5;
}
.content--wrap{
    background-color: #ffffff;
    display: grid;
    grid-template-columns: auto auto auto auto;
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    margin: 20px;
}

.item-wrap{
    background-color:#f3f3f3; 
    -webkit-box-shadow: 0px 0px 10px 0px rgba(135,130,135,1);
    -moz-box-shadow: 0px 0px 10px 0px rgba(135,130,135,1);
    box-shadow: 0px 0px 10px 0px rgba(135,130,135,1);
}
.item-wrap:hover{
    background-color:#f3f3f3; 
    -webkit-box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
    -moz-box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
    box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
}
.item-wrap img{
    width: 100%;
}
.item-wrap label{
    display: block;
    font-size: 20px;
    margin: 10px;
    text-align: center;
}
/*==============Home  Page CSS ends here =================*/



/*=============== Product Details Page CSS starts here ====================*/

.product_detail--wrap{
    background-color: #ffffff;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-column-gap: 40px;
    grid-row-gap: 40px;
    margin: 20px;

}
    .product_detail--wrap .product--image{
        text-align: center;
        padding: 20px;
    }
    .product_detail--wrap .product_detail{
        text-align: left;
        padding: 20px;
        font-size: 16x;
    }
    .addToCart--btn{
        display: block;
        padding: 14px 150px;
        color: #fff;
        font-size: 26px;
        border-radius: 12px;
        border: 1px solid #000000;
        background-color: #16ccf5;
        margin: 0px auto;
}
.addToCart--btn:hover{
    background-color:#f3f3f3; 
    color: #000000;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
    -moz-box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
    box-shadow: 0px 0px 10px 0px rgba(22,204,245,1);
    outline: none;
}
/*========================== Product Details Page CSS ends here======================= */



/* =========================Cart Page CSS starts here================================ */
.cart--container{
    width: 1024px;
    margin: 0 auto;
}

.cartDetails{
    display: grid;
    grid-template-columns: 40% 30% 30% ;
    grid-column-gap: 40px;
    margin: 25px 0px;

}

.cart_price{
    font-size: 26px;
    text-align: center;
    background-color: #cdcdcd;
}
.cart_price span{
    font-weight: bold;
}
/*================================= Cart Page CSS ends here========================== */